header {
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 20px 15%;
  display: flex;
}

.logo {
  width: 50%;
}

nav {
  flex-direction: row;
  align-self: flex-end;
  margin-left: 50%;
  display: flex;
}

nav p {
  margin-right: 2%;
  display: flex;
}

.barmenu {
  width: 40%;
}

header p {
  font-size: larger;
}

.tarjetas img {
  width: 50%;
}

img.cartel {
  width: 25%;
  height: 40%;
}

html {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
}

main {
  margin: 0 10% 10%;
}

h1, h2 {
  color: #086f37;
}

.titleindex {
  color: #000;
  text-align: center;
  padding-bottom: 5%;
  animation: 1s slide-in;
}

@keyframes slide-in {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

.tarjetas {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 10%;
  display: flex;
}

.tarjeta1, .tarjeta2, .tarjeta3 {
  width: 30%;
}

footer p {
  color: #fff;
  text-align: center;
}

footer {
  width: 100%;
  border-top: 3px ridge #000;
  align-items: center;
  display: flex;
  bottom: 0;
}

footer div {
  flex-basis: 33.3%;
}

.footerh1 {
  font-size: medium;
}

.parte2 {
  float: left;
  width: 20%;
  padding-bottom: 75px;
}

.parte1 {
  float: right;
  width: 80%;
  padding-bottom: 75px;
}

.specialp {
  font-weight: bold;
}

a:hover, a:visited {
  color: #000;
  font-weight: bold;
  text-decoration-line: none;
}

a {
  color: #000;
  font-weight: normal;
  text-decoration-line: none;
}

dt {
  font-weight: bold;
}

#map {
  height: 500px;
}

@media (min-width: 769px) {
  .imagen-responsive {
    content: url("escudo-altaresolucion.cf06eb8d.png");
    max-width: 20%;
  }

  .imagen-responsive-arte {
    content: url("fiestadelcapazo_2.a3cb8e4f.jpg");
    max-width: 20%;
  }
}

@media (max-width: 768px) and (orientation: portrait) {
  .tarjetas {
    flex-direction: column;
    align-items: center;
  }

  .tarjeta1, .tarjeta2, .tarjeta3 {
    width: 100%;
    text-align: center;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .barmenu {
    width: 80%;
  }

  .logo {
    width: 135%;
  }

  .logo-footer {
    width: 100%;
  }

  footer {
    display: grid;
    position: relative;
  }

  footer div {
    width: 80%;
    margin: auto;
  }

  .imagen-responsive {
    content: url("escudo-bajaresolucion.2bc668f9.png");
    max-width: 100%;
  }

  .imagen-responsive-arte {
    content: url("fiestadelcapazo_2_movil.0af64f7e.jpg");
    max-width: 100%;
  }

  .clip-path {
    display: none;
  }

  .cartel-mapa {
    width: 50%;
  }

  main {
    flex-direction: column;
    align-items: center;
    margin: 0 10% 10%;
    position: relative;
  }

  .parte1, .parte2 {
    width: 100%;
    text-align: center;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .cartel {
    width: 100% !important;
  }
}

.cartel-mapa {
  width: 30%;
}

.imagen-responsive {
  height: auto;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.zoom {
  justify-content: center;
  align-items: center;
  transition: transform .2s;
  display: flex;
}

.zoom img {
  width: 50%;
  height: auto;
}

.zoom:hover {
  transform: scale(1.1);
}

.clip-path {
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

